<template>
   <div class="expandable">
      <p class="expandable-category-title">{{ title }}</p>
      <div class="expandable-items-container">
         <svg
         :id="title+'-arrow'"
         class="arrow"
         v-on:click="ToggleItems"
         xmlns="http://www.w3.org/2000/svg"
         width="13"
         height="20"
         viewBox="0 0 24 16">
            <path d="M8.122 24l-4.122-4 8-8-8-8 4.122-4 11.878 12z"/>
         </svg>
         <transition-group name="item" tag="p" class="expanded-container">
         <p v-for="item in items_instance" v-bind:key="item" class="expanded-item">{{ item }}</p>
         </transition-group>
      </div>
   </div>
</template>

<script>
export default {
   name: "Expandable",
   props: {
      title: String,
      items: Array,
      rate: Number,
      color: String
  },
  data() {
     return {
        items_instance: [],
        expanded: false,
        isMoving: false,
        isMobile: false
     }
  },
  mounted() {
     // if the window is "mobile" on start...
     // then expand the items instantly,
     // disable the toggle buttons to open/close
     // if it becomes "mobile" in a desktop browser
     // this will do nothing. good.
     // because still interactable
     if(window.innerWidth <= 750 && !this.isMobile) {
         this.isMobile = true;
         this.ExpandItems(0, null);
         document.getElementsByClassName("arrow").forEach(element => {
            element.style.pointerEvents = "none";
         })
      } else if(window.innerWidth > 750 && this.isMobile) {
         this.isMobile = false;
         
         document.getElementsByClassName("arrow").forEach(element => {
            element.style.pointerEvents = "";
         })
      }
  },
  created() {
      // this is actually the arrow svg that goes by the id {{title}}-arrow
      this.$nextTick(() => {
         document.getElementById(`${this.title}-arrow`).style.fill = this.color;
      })
  },
  methods: {
     ToggleItems(e) {

         if(this.isMoving)
            return;

         var rate = this.rate;
         var target = e.target;

         // user has a chance to select the <path> element instead.
         // this makes sure only the parent <svg> element is used
         // in the contract/expand functions
         if(target.localName == "path") {
            target = e.target.parentNode;
         }

         this.isMoving = true;
         if(this.expanded) {
            this.ContractItems(rate, target);
         } else {
            this.ExpandItems(rate, target);
         }
     },
     ExpandItems(expansionRate, target) {
         if(this.expanded)
            return;

         if(target != null)
            target.classList.add("flipped-symbol");

         var i = 0;
         this.items_instance = [];
         var expansionInterval = setInterval(() => {
            this.items_instance.push(this.items[i]);

            if(i == this.items.length - 1) {
               clearInterval(expansionInterval);
               this.expanded = true;
               this.isMoving = false;
            } else {
               this.items_instance[i] += ",";
            }
            i++;
        }, expansionRate);
     },
     ContractItems(contractionRate, target) {
         if(!this.expanded)
            return;

         target.classList.remove("flipped-symbol");
         
         var i = this.items_instance.length;
         var contractionInterval = setInterval(() => {
            this.items_instance.pop();
            if(i == 1) {
               clearInterval(contractionInterval);
               this.items_instance = [];
               this.expanded = false;
               this.isMoving = false;
            }
            i--;
         }, contractionRate);
     }
  }
};
</script>

<style scoped>
.expandable {
   display: flex;
   flex-direction: row;
   padding-top: 7px;
}

.expandable-items-container {
   display: flex;
   flex-direction: row;
   height: 20px;
}

.expandable-category-title {
   width: 200px;
   font-weight: 600;
   list-style-type: disc;
}

.expanded-container {
   display: grid;
   grid-auto-flow: column;
   padding-left: 12px;
}

.expanded-item {
   padding-right: 6px;
   color: rgb(195, 195, 195);
}

.arrow {
   transition: 200ms;
}

.arrow:hover {
   opacity: 0.5;
   cursor: pointer;
}

.arrow path {
   user-select: none;
}

.item-enter-active, .item-leave-active {
   transition: opacity 1s;
}

.item-enter, .item-leave-to {
   opacity: 0;
}

.flipped-symbol {
   transform: scaleX(-100%);
}
</style>